.form-control {
  margin: 0.5rem 0;
}

.form-control label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

.form-control input {
  display: block;
  width: 100%;
  border: 1px solid #ccc;
  font: inherit;
  line-height: 1.5rem;
  padding: 0 0.25rem;
}

.form-control input:focus {
  outline: none;
  background: #b7d7e2;
  border-color: #34caff;
}
.form-control.invalid input {
  border-color: red;
  background-color: rgb(231, 176, 176);
}
.form-control.invalid label {
  color: red;
}
